export default {
  name: "testPaper-mixin",
  data() {
    return {

    }
  },

  methods: {
    //
    topicTypeName_mixin(type) {
      switch (type) {
        case 0:
          return '';
        case 1:
          return '';
        case 2:
          return '';
        case 3:
          return '';
        case 4:
          return '';
        case 5:
          return '';
      }
    },

    //
    bigQuestionIndex(index) {
      var bigQuestion = ['', '', '', '', '', ''];
      var nav_i = 0;
      for (let i = 0; i < index; i++) {
        if (this.sortedTopics[i].topic_content.length != 0) {
          nav_i += 1;
        }
      }
      return bigQuestion[nav_i];
    },

    //
    bigQuestionName_mixin(type, index) {
      let nav_i = this.bigQuestionIndex(index);
      switch (type) {
        case 0:
          return nav_i + '';
        case 1:
          return nav_i + '';
        case 2:
          return nav_i + '';
        case 3:
          return nav_i + '';
        case 4:
          return nav_i + '';
        case 5:
          return nav_i + '';
      }
    },

    //
    correctAnswer(value) {
      if (!value) return '';
      if (value.length > 200) {
        return value.slice(0, 200) + '...'
      }
      return value
    },
    //
    backTop_mixin() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    },


    //
    topicNavIndex_mixin(Topics_index, index) {
      var navIndex = 0;
      for (let i = 0; i < Topics_index; i++) {
        navIndex = navIndex + this.sortedTopics[i].topic_content.length;
      }
      return navIndex + index + 1;
    },







  },

}